// src/services/PushNotificationService.test.ts

import { describe, it, expect, jest, beforeEach, afterEach } from '@jest/globals';
import { supabase } from '../lib/supabaseClient';
import PushNotificationService from './PushNotificationService';

// --- 1. MOCKING DEPEND√äNCIAS ---

// Mock do Supabase
const mockSupabaseInstance = {
  from: jest.fn(),
};

jest.mock('../lib/supabaseClient', () => ({
  supabase: mockSupabaseInstance,
}));

// Typecast do mock para intellisense
const mockedSupabase = supabase as jest.Mocked<typeof supabase>;

// --- 2. CONFIGURA√á√ÉO DOS TESTES ---

describe('PushNotificationService', () => {

  // Espi√µes (Spies) para os m√©todos est√°ticos que s√£o chamados internamente
  let spySendPush: jest.SpiedFunction<typeof PushNotificationService.sendPush>;
  let spyLogPush: jest.SpiedFunction<any>; // 'any' para espionar m√©todo privado
  let spySendToDevice: jest.SpiedFunction<any>; // 'any' para espionar m√©todo privado

  beforeEach(() => {
    jest.clearAllMocks();
    (mockedSupabase.from as jest.Mock).mockReset();

    // Recria os espi√µes antes de cada teste
    // Espionamos sendPush para testes de orquestra√ß√£o (ex: sendPasswordToHost)
    spySendPush = jest.spyOn(PushNotificationService, 'sendPush')
      .mockImplementation(() => Promise.resolve({ success: true }));

    // Espionamos os m√©todos privados para verificar se s√£o chamados
    spyLogPush = jest.spyOn(PushNotificationService as any, 'logPushNotification')
      .mockImplementation(() => Promise.resolve());
      
    spySendToDevice = jest.spyOn(PushNotificationService as any, 'sendPushToDevice')
      .mockImplementation(() => Promise.resolve());
  });

  afterEach(() => {
    // Restaura os espi√µes ap√≥s cada teste
    spySendPush.mockRestore();
    spyLogPush.mockRestore();
    spySendToDevice.mockRestore();
  });

  // --- 3. TESTES DOS M√âTODOS ---

  describe('sendPush (M√©todo Principal)', () => {
    it('deve enviar push para m√∫ltiplos devices e registrar log', async () => {
      // Libera o mock de 'sendPush' para testar sua l√≥gica interna
      spySendPush.mockRestore(); 
      
      const mockDevices = [
        { token: 'token-web', platform: 'web' },
        { token: 'token-ios', platform: 'ios' },
      ];

      // 1. Mock do SELECT de tokens
      (mockedSupabase.from as jest.Mock).mockImplementationOnce(() => ({
        select: jest.fn(() => ({
          eq: jest.fn(() => ({
            eq: jest.fn().mockImplementation(() => Promise.resolve({ data: mockDevices, error: null })),
          })),
        })),
      }));

      const params = {
        userId: 'user-123',
        title: 'T√≠tulo Teste',
        body: 'Corpo Teste',
        eventId: 1,
      };

      const result = await PushNotificationService.sendPush(params);

      expect(result.success).toBe(true);
      expect(result.message).toBe('Enviada para 2 dispositivos');
      
      // Verifica se buscou os tokens
      expect(mockedSupabase.from).toHaveBeenCalledWith('user_device_tokens');
      
      // Verifica se tentou enviar para os 2 devices
      expect(spySendToDevice).toHaveBeenCalledTimes(2);
      expect(spySendToDevice).toHaveBeenCalledWith(expect.objectContaining({ token: 'token-web' }));
      expect(spySendToDevice).toHaveBeenCalledWith(expect.objectContaining({ token: 'token-ios' }));
      
      // Verifica se registrou o log
      expect(spyLogPush).toHaveBeenCalledWith(
        expect.objectContaining({
          user_id: 'user-123',
          devices_sent: 2,
          total_devices: 2,
        })
      );
    });

    it('n√£o deve fazer nada se o usu√°rio n√£o tiver tokens', async () => {
      // Libera o mock de 'sendPush' para testar sua l√≥gica interna
      spySendPush.mockRestore(); 
      
      // 1. Mock do SELECT de tokens (retornando vazio)
      (mockedSupabase.from as jest.Mock).mockImplementationOnce(() => ({
        select: jest.fn(() => ({
          eq: jest.fn(() => ({
            eq: jest.fn().mockImplementation(() => Promise.resolve({ data: [], error: null })),
          })),
        })),
      }));

      const params = { userId: 'user-404', title: 'Teste', body: '...' };
      const result = await PushNotificationService.sendPush(params);

      expect(result.success).toBe(true);
      expect(result.message).toBe('Nenhum dispositivo registrado');
      
      // Garante que n√£o tentou enviar nem registrar log
      expect(spySendToDevice).not.toHaveBeenCalled();
      expect(spyLogPush).not.toHaveBeenCalled();
    });
  });

  describe('sendPasswordToHost', () => {
    it('deve inserir notifica√ß√£o no DB e chamar sendPush', async () => {
      // 1. Mock do INSERT da notifica√ß√£o
      const mockInsert = jest.fn().mockImplementation(() => Promise.resolve({ error: null }));
      (mockedSupabase.from as jest.Mock).mockReturnValue({
        insert: mockInsert,
      });

      const result = await PushNotificationService.sendPasswordToHost(
        'host-id', 1, '1234', 'Evento Senha'
      );

      expect(result.success).toBe(true);

      // Verifica se inseriu a notifica√ß√£o no DB
      expect(mockedSupabase.from).toHaveBeenCalledWith('notifications');
      expect(mockInsert).toHaveBeenCalledWith(
        expect.objectContaining({
          user_id: 'host-id',
          event_id: 1,
          notification_type: 'event_password',
          data: { password: '1234' },
        })
      );

      // Verifica se chamou o 'sendPush' (que est√° espionado)
      expect(spySendPush).toHaveBeenCalledWith(
        expect.objectContaining({
          userId: 'host-id',
          title: 'üîë Sua Senha do Evento',
          body: expect.stringContaining('1234'),
          data: expect.objectContaining({ password: '1234' }),
        })
      );
    });
  });

  describe('sendEventStartNotificationToParticipants', () => {
    it('deve chamar sendPush para cada participante', async () => {
      const participants = ['part-1', 'part-2'];
      await PushNotificationService.sendEventStartNotificationToParticipants(
        participants, 2, 'Evento Come√ßando'
      );

      // Verifica se 'sendPush' (espionado) foi chamado 2 vezes
      expect(spySendPush).toHaveBeenCalledTimes(2);

      // Verifica a chamada para o primeiro participante
      expect(spySendPush).toHaveBeenCalledWith(
        expect.objectContaining({
          userId: 'part-1',
          eventId: 2,
          title: 'üéâ Seu Evento est√° Come√ßando!',
        })
      );
      // Verifica a chamada para o segundo participante
      expect(spySendPush).toHaveBeenCalledWith(
        expect.objectContaining({
          userId: 'part-2',
          eventId: 2,
          title: 'üéâ Seu Evento est√° Come√ßando!',
        })
      );
    });
  });

  describe('registerDeviceToken', () => {
    it('deve chamar insert em user_device_tokens', async () => {
      const mockInsert = jest.fn().mockImplementation(() => Promise.resolve({ error: null }));
      (mockedSupabase.from as jest.Mock).mockReturnValue({
        insert: mockInsert,
      });

      await PushNotificationService.registerDeviceToken('user-id', 'token-teste', 'web');

      expect(mockedSupabase.from).toHaveBeenCalledWith('user_device_tokens');
      expect(mockInsert).toHaveBeenCalledWith(
        expect.objectContaining({
          user_id: 'user-id',
          token: 'token-teste',
          platform: 'web',
          is_active: true,
        })
      );
    });
  });

  describe('unregisterDeviceToken', () => {
    it('deve chamar update em user_device_tokens', async () => {
      const mockUpdateEq = jest.fn().mockImplementation(() => Promise.resolve({ error: null }));
      (mockedSupabase.from as jest.Mock).mockReturnValue({
        update: jest.fn(() => ({
          eq: mockUpdateEq,
        })),
      });

      await PushNotificationService.unregisterDeviceToken('token-para-remover');

      expect(mockedSupabase.from).toHaveBeenCalledWith('user_device_tokens');
      expect(mockedSupabase.from('user_device_tokens').update).toHaveBeenCalledWith({ is_active: false });
      expect(mockUpdateEq).toHaveBeenCalledWith('token', 'token-para-remover');
    });
  });

});
